<template>
  <group class="jq-mt">
  <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
   </group>
 <group class="jq-mt">
   <div class="chart" v-ebarline="barLineData"></div>
    </group>
     <group class="jq-mt">
      <div class="chart" v-epie="pieData" ></div>
     </group>
</template>
  <script>
    //import {Group,Selector,PopupPicker} from 'vux/src/components'
    import Group from '*/group'
    import Selector from '*/selector'
    import PopupPicker from '*/popup-picker'
    import { getList } from '../../../untils/api'
    export default {
    components: {
    Group,
    PopupPicker,
    Selector
    },
    data () {
    return {
    barLineData:{},
    dateTile:'日期',
    time: formatData.defaultTime,
    timeList: formatData.dateList,
    pieData:{}
    }
    },
    ready(){
      document.title='品质管理';
    },
    methods: {
    async getData() {
        commit('UPDATE_LOADING', true)
    let id ='APP10212';
    let date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){

    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barLineData = {};
    this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    },
     async getPieData() {
         commit('UPDATE_LOADING', true)
    let id ='APP10211';
    let date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){

    //表头：
    //表格:
    const TableList = dataTit.data;
    this.pieData = {};
    this.pieData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    }
    },
    watch: {
     time (newtime, oldtime) {
      this.getData();
       this.getPieData();
        formatData.setDateTime(newtime);
     }
    },
    created: function () {
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.pieData={};
    this.pieData.dataset=[];
    this.getData();
    this.getPieData();
    }
    }
  </script>






